import React, { Component, Fragment } from 'react';
import { Row, Col, Form, Select, Button, DatePicker, Input, Checkbox, Radio } from 'antd';
import FilterPathEducate from 'components/common/input/FilterPathEducate';

const { Option } = Select;

@Form.create({ name: 'formSerchIndividual' })
class FormSerchIndividual extends Component {
  state = {};
  handleReset = () => {
    this.props.form.resetFields();
  };
  handleSearch = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      console.log('Received values of form: ', values);
    });
  };
  render() {
    return (
      <Form layout="vertical" onSubmit={this.handleSearch}>
        <Row gutter={24}>
          <FilterPathEducate form={this.props.form} />
        </Row>
      </Form>
    );
  }
}

export default FormSerchIndividual;



