import React, { Component, Fragment } from 'react';
import { Row, Col, Form, Select, Button, DatePicker, Input, Checkbox, Radio } from 'antd';
import FilterPathEducate from 'components/common/input/FilterPathEducate';
import {
  BtnAddNew,
  BtnCancel,
  BtnDelete,
  BtnEdit,
  BtnSearch,
  BtnClear,
  BtnSave,
} from 'components/common/button';

const { Option } = Select;

@Form.create({ name: 'formSerchIndividual' })
class FormSerchIndividual extends Component {
  state = {};
  handleReset = () => {
    this.props.form.resetFields();
  };
  handleSearch = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      console.log('Received values of form: ', values);
    });
  };
  render() {
    const { getFieldDecorator } = this.props.form;
    return (
      <Form layout="vertical" onSubmit={this.handleSearch}>
        <Row gutter={24}>
          <Col span={8}>
            <Form.Item label="Số BD">{getFieldDecorator('sobd', {})(<Input />)}</Form.Item>
          </Col>
          <Col span={8}>
            <Form.Item label="Tên">{getFieldDecorator('name', {})(<Input />)}</Form.Item>
          </Col>
          <Col span={8}>
            <Form.Item label="Ngày sinh">{getFieldDecorator('ngaysinh', {})(<Input />)}</Form.Item>
          </Col>
        </Row>
        <Row>
          <Col span={24} style={{ textAlign: 'right' }}>
            <div className="btn-group">
              <BtnAddNew />
              <BtnCancel />
              <BtnClear />
              <BtnDelete />
              <BtnEdit />
              <BtnSave />
              <BtnSearch />
            </div>
          </Col>
        </Row>
      </Form>
    );
  }
}

export default FormSerchIndividual;



